.ck-content h1,
.ck-content h2,
.ck-content h3,
.ck-content h4,
.ck-content h5,
.ck-content h6,
.ck-content p,
.ck-content ul,
.ck-content li,
.ck-content blockquote {
  margin-top: 0;
  padding: 0;
}
.ck-content h3 {
  font-size: 2.4em;
}
.ck-content p,
.ck-content li {
  font-size: 1.6em;
  line-height: 1.5em;
  text-align: left;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
}
.ck-content .align-center,
.ck-content .text-align-center {
  text-align: center;
}
.ck-content a.button {
  color: #ffffff;
  padding: 1em 1.75em;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.125em;
  font-style: normal;
  font-weight: 800;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  border-radius: 2em;
}
.ck-content a.button strong,
.ck-content a.button b {
  font-weight: 900;
}
.ck-content a.button.teal {
  background-color: #7ba1af;
}
.ck-content a.button.orange {
  background-color: #c9a764;
}
.ck-content a.button.violet {
  background-color: #968c78;
}
.ck-content a.button:hover {
  opacity: 0.8;
}
.ck-content a.button:active {
  opacity: 1;
}